.top-left {
  position: absolute;
  top: 20px;
  left: 20px;
  width: 80%;
}

.bottom-left {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.single, .double{
  position: relative;
}

.single div {
  height: 200px;
}

.double div {
  height: 416px;
}

.store-header {
  font-weight: 600;
  line-height: 32px;
  letter-spacing: 0.03em;
  text-transform: capitalize;
}

.hr-line {
  border-top: 0.1rem solid var(--featured-store-element-header-line);
  width: 50%;
}

@media (max-width: 970px) {
  .store-header {
    font-size: 1.3rem;
  }

  .store-button {
    font-size: 0.8rem;
  }
}

@media (max-width: 767px) {
  .double div {
    height: 408px;
  }

  .top-left {
    top: 15px;
    left: 15px;
  }

  .bottom-left {
    bottom: 15px;
    left: 15px;
  }

  .store-header {
    font-size: 1.1rem;
    line-height: 24px;
  }

  .store-button {
    padding: 0.25rem 0.5rem;
    font-size: 0.7rem;
  }
}

@media (max-width: 575px) {
  .double div {
    height: 200px;
  }
}

@media (max-width: 450px) {
  .single div {
    height: 150px;
  }

  .double div {
    height: 150px;
  }

  .top-left {
    top: 10px;
    left: 10px;
  }

  .bottom-left {
    bottom: 10px;
    left: 10px;
  }

  .store-header {
    font-size: 0.8rem;
    margin-bottom: .15rem;
  }

  .store-button {
    padding: 0.125rem 0.25rem;
    font-size: 0.6rem;
  }
}
