.header-bar{
    background: rgb(45,45,32);
    background: linear-gradient(45deg, rgba(45,45,32,1) 0%, rgba(61,61,49,1) 50%, rgba(45,45,45,1) 100%);
    display: flex;
    align-items: baseline;
    position: sticky;
    top: 0;
    z-index: 100;
    padding: 0.5rem;
    justify-content: space-between;
}

.mobile {
  display: none;
}

.mobile-mini {
  display: none;
}

.header-bar > #nav-check {
  display: none;
}

.header-logo {
    margin: 0 20px;
    cursor: pointer;
    height: auto;
    width: 160px;
}

.header-logo-img {
    display: inline-block;
    vertical-align: top;
    max-width: 100%;
    max-height:100%;
}

.header-nav {
    flex-direction: column;
}

.header-nav-item-list {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-top: 10px;
    margin-bottom: 10px;
}

.header-nav-item {
    color: var(--header-white);
    text-decoration: inherit;
    margin-left: 15px;
    margin-right: 15px;
    margin-top: 5px;
    margin-bottom: 5px;
}

.header-nav-item:hover {
    color: var(--button-primary);
    text-decoration: inherit;
}

.header-nav-item.active {
    color: var(--button-primary);
    text-decoration: inherit;
}

.header-nav-search-bar {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: center;
    padding: 10px;
    padding-top: 20px;
}

.header-nav-search-bar-input {
    border: none;
    height: calc(1.5em + .75rem + 7px);
}

.header-nav-search-bar-prepend {
    border: none;
    background-color: var(--header-white);
    color: var(--header-search-icon);
}

.header-cart-logo {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    cursor: pointer;
    font-size: 1.8rem;
    color: var(--button-primary);
}

.header-cart-logo > span {
	width: 20px;
	height: 20px;
	font-size: 11px;
	color: var(--header-white);
	line-height: 20px;
	position: absolute;
	-moz-border-radius: 12px;
	-webkit-border-radius: 12px;
	border-radius: 12px;
	top: 0;
	right: 0;
	background: var(--header-cart-count-background);
}

.header-cart-logo > span.counter {
	display: flex;
    justify-content: center;
}

.header-login {
    display: flex;
    flex-direction: row;
    color: var(--header-white);
    cursor: pointer;
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 5px;
    margin-bottom: 5px;
}

.header-login:hover {
    color: var(--button-primary);
}

.header-login-item {
    padding: 10px;
}

.header-profile {
    display: flex;
    flex-direction: row;
    color: var(--header-white);
    cursor: pointer;
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 5px;
    margin-bottom: 5px;
}

.header-profile:hover {
    color: var(--button-primary);
}

.header-profile-item {
    display: flex;
    flex-direction: column;
    align-self: center;
    padding: 10px;
}

.header-profile-item > span {
	font-size: 11px;
}

.header-mobile-bar {
    display: none;
}

@media (max-width: 967px) {

    .header-bar {
        padding: 0;
    }

    .header-logo {
        margin: 0 10px;
        padding-bottom: 10px;
        width: 140px;
    }

    .header-nav-item-list {
        margin-top: 5px;
        margin-bottom: 5px;
    }

    .header-nav-item {
        margin-left: 10px;
        margin-right: 10px;
        font-size: 0.9rem;
        font-weight: 300;
    }

    .header-nav-search-bar {
        padding-top: 10px;
    }

    .header-nav-search-bar-prepend {
        font-size: 0.9rem;
        font-weight: 300;
    }

    .header-nav-search-bar-input {
        font-size: 0.9rem;
        font-weight: 300;
        height: calc(1.5em + .75rem + 4px);
    }

    .header-cart-logo {
        font-size: 1.5rem;
    }

    .header-cart-logo > span {
        width: 15px;
        height: 15px;
        font-size: 9px;
        font-weight: 300;
        line-height: 15px;
        -moz-border-radius: 12px;
        -webkit-border-radius: 12px;
        border-radius: 12px;
    }

    .header-login-item {
        padding: 5px;
        font-size: 0.9rem;
        font-weight: 300;
    }

    .header-profile-item {
        padding: 5px;
        font-size: 0.9rem;
        font-weight: 300;
    }

    .header-profile-item > span {
        font-size: 10px;
    }
}

@media (max-width: 780px) {
    .web {
        display: none;
    }

    .mobile {
      display: inherit;
    }

    .header-bar {
        align-items: center;
    }

    .header-logo {
        height: 75px;
        margin: 10px;
        padding: 0;
    }

    .header-logo-img {
      max-width: unset;
    }

    .header-mobile-bar {
        display: block;
        color: var(--button-primary);
        font-size: 1.5rem;
        font-weight: 300;
        margin-right: 10px;
    }

    .header-nav {
        position: absolute;
        width: 100%;
        background-color: var(--header-nav-dropdown-background);
        transition: max-height 0.4s ease-in;
        overflow-y: hidden;
        top: 95px;
        left: 0px;
    }

    .header-nav-item-list {
        flex-direction: column;
        height: 270px;
        overflow-y: scroll;
        padding: 15px;
        justify-content: flex-start;
    }

    .header-nav-item {
        padding-top: 10px;
        padding-bottom: 10px;
    }

    .header-nav-search-bar {
        padding-bottom: 20px;
    }

    .header-bar > #nav-check:checked ~ .header-nav {
        max-height: 400px;
        overflow-y: auto;
    }

    .header-bar > #nav-check:not(:checked) ~ .header-nav {
        max-height: 0px;
    }
}

@media (max-width: 600px) {

  .header-logo {
      height: 70px;
      margin: 10px;
      padding: 0;
  }

  .header-nav {
      top: 90px;
  }
}

@media (max-width: 380px) {

  .web-mini {
    display: none;
  }

  .mobile-mini {
    display: inherit;
  }
}

@media (max-width: 350px) {

  .header-logo {
      height: 60px;
      margin: 10px;
      padding: 0;
  }

  .header-nav {
      top: 80px;
  }
}

.header-search-box{
    margin-top: -1px;
}
