
.form{
    width: 100%;
}

.form-icon-btn{
    margin: 20px;
    padding: 0px !important;
}

.form-text-box{
    box-shadow: 0px 3px 3px 0px var(--form-shadow);
    border: none;
    border-radius: 0%;
}

.form-button-submit{
    width: 100%;
    border-radius: 0%;
}
.form-container{
    background-color: var(--form-background);
    padding: 7%;
}
.form-federated-row{
    text-align: center;
    display: inherit;
}
.form-checkbox{
    width: 1rem;
    height: 1rem;
    position: relative !important;
    margin: 0% !important;
    float: left ;
}
.form-checkbox-text{
    float: left;
    margin: 10px;
}
.form-remember-me{
    display: inline-flex;
    align-items: center;
}
.form-text{
    display: inline;
}
.form-link-text, .form-link-text:hover, .form-link-text:focus{
    color: var(--form-link);
    cursor: pointer;
    display: inline;
}