.profile_line-box {
    text-indent: 7px;
    box-shadow: 0 3px 3px 0 var(--form-shadow);
    border: none;
    border-radius: 0;
}

.profile {
    padding: 0 0 5px 0;
}

.email {
    text-decoration: underline;
}

.name {
    font-weight: bolder !important;
    color: var(--user-profile-name);
}

.padding_top_bottom {
    padding-top: 15px;
    padding-bottom: 15px;
}

.active {
    color: var(--user-profile-verified-symbol-active);
}

.save_button {
    text-align: right;
    display: grid;
}

.verified_icon {
    color: var(--user-profile-verified-symbol-inactive);
    font: caption;
}

.address_book_header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 1.5rem;
}

.edit_class {
    padding-top: 8px;
    color: var(--label-gold);
}

.vl {
    border-left: 2px solid var(--user-profile-address-seperater);
    min-height: auto;
}

.table_rows {
    background-color: var(--background);
    color: var(--label-darkgrey);
}

.manage_class {
    color: var(--label-gold);
}

.main_table_container {
    overflow-x: auto;
}

.main_table {
    min-width: 695px;
    content-visibility: auto;
    padding-bottom: 120px;
}

.whole_page {
    overflow-x: hidden;
}

.address_book_content {
    padding-left: 10px;
}
