.carousel {
    height:75vh;
} 

.carousel_indicators li {
    border-radius: 50%;
    width: 10px;
    height: 10px;
}

.carousel_caption1 {
    top: 10%;
    left: 12%;
    width: 60%;
    height: 50%;
    bottom: auto;
    overflow: visible;
}

.carousel_caption2 {
    top: auto;
    left: auto;
    bottom:0;
    justify-content: center;
    width: inherit;
}

.carousel .carousel_img_container img {
    width:auto;
    height: 100%;
    display: block;
    pointer-events: none;
}

.carousel_img_container {
    height: inherit;
    width: inherit;
    display: flex;
    justify-content: center;
    align-content: center;
}

.carousel_control_next_icon {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%362f19' width='8' height='8' viewBox='0 0 8 8'%3e%3cpath d='M2.75 0l-1.5 1.5L3.75 4l-2.5 2.5L2.75 8l4-4-4-4z'/%3e%3c/svg%3e");
    display:none
}

.carousel_control_prev_icon {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%362f19' width='8' height='8' viewBox='0 0 8 8'%3e%3cpath d='M5.25 0l-4 4 4 4 1.5-1.5L4.25 4l2.5-2.5L5.25 0z'/%3e%3c/svg%3e");
    display:none
}

.carousel_container {
    width: 100%;
    box-shadow: 4px 4px 4px 0 rgba(0, 0, 0, 0.2), 6px 6px 6px 0 rgba(0, 0, 0, 0.1);
}

.carousel_container:hover .carousel_control_prev_icon {
    display: block;
}

.carousel_container:hover .carousel_control_next_icon {
    display: block;
}

.overlay::after{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background:transparent;
    background: linear-gradient(170deg, rgba(255,254,251,0.8337534843038779) 35%, 
    rgba(255,254,252,0.6572828960685837) 40%, rgba(255,254,252,0.27072827421984413) 44%,
    rgba(255,255,255,0) 100%);
    pointer-events: none;
    content: '';
}

.carousel_bottom_left {
    top:auto;
    right: auto;
    left:6%;
    bottom: 6%;
}

.carousel_bottom_right {
    top:auto;
    left:auto;
    right: 6%;
    bottom: 6%;
}

.carousel_bottom_center {
    top:auto;
    left:auto;
    right:auto;
    bottom: 6%;
}

@media screen and (max-width: 1200px) { 
    .carousel {
        height: 65vh;
    }
}

@media screen and (max-width: 992px) { 

    .carousel_caption1 {
        top: 6%;
        left: 10%;
        width: 70%;
        height: 70%;
        justify-content: center;
    }
    
    .carousel {
        height: 60vh;
    }
}

@media screen and (max-width: 768px) { 
    .carousel {
        height: 35vh;
    }

    .carousel_caption1 {
        top: 6%;
        left: 10%;
        width: 80%;
        height: 70%;
        justify-content: center;
    }

    .carousel_container:hover .carousel_control_prev_icon {
        display: none;
    }

    .carousel_container:hover .carousel_control_next_icon {
        display: none;
    }

    .carousel_bottom_left {
        top:auto;
        left:auto;
        right:auto;
        bottom: 8%;
    }
    
    .carousel_bottom_right {
        top:auto;
        left:auto;
        right:auto;
        bottom: 8%;
    }
    
    .carousel_bottom_center {
        top:auto;
        left:auto;
        right:auto;
        bottom: 8%;
    }
    
    .carousel_center {
        top:auto;
        left:auto;
        right:auto;
        bottom: 8%;
    }
}

@media screen and (max-width: 450px) { 
    .carousel {
        height: 30vh;
    }

    .carousel_caption1 {
        top: 0;
        left: 10%;
        width: 80%;
        height: 40%;
        justify-content: center;
    }
    
    .carousel_bottom_left {
        top:auto;
        left:auto;
        right:auto;
        bottom: 9%;
    }
    
    .carousel_bottom_right {
        top:auto;
        left:auto;
        right:auto;
        bottom: 9%;
    }
    
    .carousel_bottom_center {
        top:auto;
        left:auto;
        right:auto;
        bottom: 9%;
    }
    
    .carousel_center {
        top:auto;
        left:auto;
        right:auto;
        bottom: 9%;
    }

    .carousel_container:hover .carousel_control_prev_icon {
        display: none;
    }

    .carousel_container:hover .carousel_control_next_icon {
        display: none;
    }
}
