.filterDropDown {
    padding: 2rem 2rem 0 2rem;
    text-align: left;
}

.filterLabel {
    text-align: left;
}

.area_filter_container {
    display: block;
    min-width: 210px;
    padding: 2rem 2rem 0 2rem;
}
.area_filter_component {
    display: block;
    background-color: var(--search_page-section-background);
    padding: 1rem 1rem;
    box-shadow: 0 2px 5px 1px rgb(0 0 0 / 20%);
    border-radius: 0.25rem;
}

.area_filter_content {
    margin: 0.5rem 0.5rem;
}

.item_container {
    flex-wrap: wrap;
    justify-content: left;
}