.default-label-style {
  color: black;
  font-family: "Taviraj", serif;
  font-size: medium;
}

.primary {
  color: var(--label-primary);
  font-family: "Taviraj", serif;
  line-height: 2.2rem;
  font-size: 2rem;
  font-weight: 700;
}

.secondary {
  color: var(--label-secondary);
  font-family: "Taviraj", serif;
  line-height: 1.7rem;
  font-size: 1.5rem;
  font-weight: 500;
}

.tertiary {
  color: var(--label-tertiary);
  font-family: "Taviraj", serif;
  line-height: 1rem;
  font-size: 0.9rem;
  font-weight: 400;
}

.deleted {
  text-decoration: line-through;
  text-decoration-color: var(--error);
}

.default-label {
  color: black;
  font-family: "Taviraj", serif;
}

.italic-label {
  font-style: italic;
}

.bold-label {
  font-weight: 600;
}

.underlined{
  text-decoration: underline;
}

.delete-label {
  text-decoration: line-through;
  text-decoration-color: var(--error);
}

.primary-color {
  color: var(--label-primary);
}

.secondary-color {
  color: var(--label-secondary);
}

.tertiary-color {
  color: var(--label-tertiary);
}

.darkgrey-color {
  color: var(--label-darkgrey);
}

.lightgrey-color {
  color: var(--label-lightgrey);
}

.gold-color {
  color: var(--label-gold);
}

.dark-brown-color {
  color: var(--label-dark-brown);
}

.h1-label {
  font-size: 32px;
  letter-spacing: 0.02em;
}

.h2-label {
  font-size: 28px;
  letter-spacing: 0.02em;
}

.h3-label {
  font-size: 24px;
  letter-spacing: 0.02em;
}

.h4-label {
  font-size: 20px;
  letter-spacing: 0.02em;
}

.h5-label {
  font-size: 16px;
  letter-spacing: 0.02em;
}

.h6-label {
  font-size: 12px;
  letter-spacing: 0.02em;
}

@media (max-width: 767px) {
  .h1-label {
    font-size: 27px;
    letter-spacing: normal;
  }

  .h2-label {
    font-size: 23px;
    letter-spacing: normal;
  }

  .h3-label {
    font-size: 19px;
    letter-spacing: normal;
  }

  .h4-label {
    font-size: 17px;
    letter-spacing: normal;
  }

  .h5-label {
    font-size: 13px;
    letter-spacing: normal;
  }

  .h6-label {
    font-size: 10px;
    letter-spacing: normal;
  }
}

@media (max-width: 575px) {
  .h1-label {
    font-size: 24px;
  }

  .h2-label {
    font-size: 20px;
  }

  .h3-label {
    font-size: 16px;
  }

  .h4-label {
    font-size: 13px;
  }

  .h5-label {
    font-size: 11px;
  }

  .h6-label {
    font-size: 9px;
  }
}
