.btn {
    font-size: 0.9em;
    border-width:0.1rem ;
    border-style:solid;
    border-radius: 0.3rem;
    font-family: "Taviraj";
    font-weight:900;
    font-size: 0.9em;
    box-shadow: none !important;
    outline: none !important;
    outline-offset: unset;
}

.btn:focus {
    box-shadow: none;
    outline: none;
    outline-offset: unset;
}

.btn-primary {
    color:var(--button-primary-text);
    background-color: var(--button-primary);
    border-color: var(--button-tertiary);
}

.btn-primary:hover:enabled {
    background-color:var(--button-tertiary);
    color:var(--button-primary-text);
    border-color: var(--button-tertiary);
}

.btn-primary:hover:active:enabled {
    background-color:var(--button-tertiary);
    color:var(--button-primary-text) ;
    border-color: var(--button-tertiary) ;
}

.btn-primary:focus {
    background-color:var(--button-tertiary);
    color:var(--button-secondary-text);
    border-color: var(--button-tertiary);
    box-shadow: none;
    outline: none;
    outline-offset: unset;
}

.btn-primary:disabled {
    color:var(--button-primary-text);
    background-color: var(--button-primary);
    border-color: var(--button-tertiary);
    opacity: .65;
}

.btn-primary:not(:hover):enabled {
    color:var(--button-primary-text);
    background-color: var(--button-primary);
    border-color: var(--button-tertiary);
}

.btn-secondary {
    color: var(--button-secondary-text);
    background-color: var(--button-secondary); 
    border-color: var(--button-primary);
}

.btn-secondary:hover:active:enabled {
    background-color:var(--button-tertiary);
    color:var(--button-secondary-text);
    border-color: var(--button-tertiary);
}

.btn-secondary:hover:enabled {
    background-color:var(--button-tertiary);
    color:var(--button-secondary-text);
    border-color: var(--button-tertiary);
}

.btn-secondary:focus {
    background-color:var(--button-tertiary);
    color:var(--button-secondary-text);
    border-color: var(--button-tertiary);
}

.btn-secondary:disabled {
    color:var(--button-primary-text);
    background-color: var(--button-primary);
    border-color: var(--button-tertiary);
    opacity: .65;
}

.btn-secondary:not(:hover):enabled {
    color: var(--button-secondary-text);
    background-color: var(--button-secondary);
    border-color: var(--button-primary);
}

.btn-link {
    color: var(--button-link-text);
}

.btn-link:hover, .btn-link:focus {
    color: var(--button-tertiary);
    text-decoration: none;
}

.btn-link:not(:hover):enabled {
    color: var(--button-link-text);
}
@media (max-width: 970px) {
    .responsive-btn {
        font-size: 0.9rem;
    }
}

@media (max-width: 767px) {
    .responsive-btn {
        font-size: 0.85rem;
    }
}

@media (max-width: 575px) {
    .responsive-btn {
        font-size: 0.82rem;
    }
}

@media (max-width: 320px) {
    .responsive-btn {
        font-size: 0.7rem;
    }
}