.footer {
    background: var(--common-footer-background);
    width: 100%;
    font-family: Taviraj;
    font-weight: bold;
    font-size: 16px;
    padding: 23px 23px 8px 23px;
    margin-top: 50px;
    display: inline-block;
    vertical-align: top;
}

.footer .about {
    line-height: 20px;
    color: var(--common-footer-about-text);
    font-size: 18px;
    padding-bottom: 52px;
    font-weight: 100;
    text-align: justify;
    text-align-last: center;
    margin-bottom: -2rem;
}

.footer .about span {
    display: block;
    color: var(--common-footer-about-text);
    font-size: 40px;
    margin-bottom: 20px;
}

.footer .icons {
    text-align: center;
}

.footer .icons a {
    display: -webkit-inline-flex;
    cursor: pointer;
    border-radius: 25px;
    font-size: 14px;
    color: var(--common-footer-icon);
    text-align: center;
    line-height: 11px;
    margin-right: 19px;
}

.footer .icons a p{
    margin-left: 5px;
    font-weight: 100;
}

.footer .menu {
    color: var(--common-footer-menu-text);
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    padding-bottom: 37px;
    letter-spacing: 0.03em;
    margin-bottom: -2rem;
}

.footer .menu a {
    display: inline-block;
    text-decoration: none;
    color: inherit;
    margin: 0 0 0 4px;
}

.footer .menu a:hover {
    color: var(--common-footer-menu-hover);
}

.footer .images-gellary{
    list-style: none;
    position: relative;
    text-align: center;
}

@media (max-width: 767px) {
    .footer {
        font-size: 14px;
    }
    .footer {
        display: block;
        width: 100%;
        margin-bottom: 40px;
        text-align: justify;
        text-align-last: center;
    }
}
