.span-clear{
    display: flex !important;
    flex-direction: row-reverse !important;
    margin : 10px 10px 10px 10px;
}

.span-clear-text{
    font-size: 16px;
    background-color: var(--searchable-dropdown-background);
    color: var(--searchable-dropdown-white);
    width : 70px;
    height: 30px;
    text-align: center;
    top: 10px;
    border-radius: 10px;
    cursor: pointer;
}

.span-clear-text:hover{
    font-size: 16px;
    background-color: var(--searchable-dropdown-background);
    color: var(--searchable-dropdown-white);
    width : 70px;
    height: 30px;
    text-align: center;
    top: 10px;
    border-radius: 10px;
    cursor: pointer;
}

.span-clear-text-disable{
    font-size: 16px;
    background-color: var(--searchable-dropdown-background);
    opacity: 50%;
    color: var(--searchable-dropdown-white);
    width : 70px;
    height: 30px;
    text-align: center;
    top: 10px;
    border-radius: 10px;
    cursor: pointer;
}

.box-shadow{
    box-shadow: 0px 4px 4px rgba(51, 51, 51, 0.04), 0px 4px 16px rgba(51, 51, 51, 0.08);
}

.selected-items-container{
    padding: 10px 10px 10px 10px;
    margin: 10px 10px 10px 10px;
}

.input-box-container{
    padding: 10px 10px 10px 10px;
    margin: 10px 10px 10px 10px;
    display: flex;
    flex-direction: row;
}

.input-box{
    border-color: var(--searchable-dropdown-text);
    border-style: none !important;
    margin:  0 0 0 0;
    flex-grow: 10;
}

input:focus{
    border-color: var(--searchable-dropdown-text);
    border-style: none !important;
    margin:  0 0 0 0;
    outline: 0 !important;
}

.input-close-span{
    flex-grow: 1;
    color: var(--searchable-dropdown-grey);
}

.dropdown-content{
    padding: 10px 10px 10px 10px;
    margin: 10px 10px 10px 10px;
    max-height: 200px;
    overflow: scroll !important;
    display: block;
}

.dropdown-item-label{
    font-size: 16px;
    padding: 8px 8px 8px 8px;
    display: inherit;
    z-index: inherit;
    cursor: pointer;
}

.dropdown-item-label-disable{
    font-size: 16px;
    padding: 8px 8px 8px 8px;
    display: inherit;
    color: var(--searchable-dropdown-lightslategray);
}

.dropdown-item-label:hover{
    background-color: var(--searchable-dropdown-background);
    color: var(--searchable-dropdown-text);
    padding: 8px 8px 8px 8px;
    display: inherit;
    cursor: pointer;
}

.dropdown-content-hidden{
    display: none;
}

.input-tick-span{
    float: right;
}

.input-tick-span-hide{
    display: none;
    float: right;
}