.padding_tb {
  padding-top: 15px;
  padding-bottom: 15px;
}

.padding_t {
  padding-top: 15px;
}

.padding_b {
  padding-bottom: 15px;
}

.info_card_flex {
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: var(--information-card-background);
  width: 100%;
  padding-top: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
}

.info_card_flex_d {
  display: flex;
  flex-direction: column;
  background-color: var(--information-card-background);
  width: 100%;
  padding-top: 1rem;
}

.info_card {
  background-color: var(--information-card-background);
  padding: 0 1rem 1rem 1rem;
  width: 100%;
}

.info_image {
  padding: 1rem;
}

.divider_line {
  border-top: 0.1rem solid var(--information-card-line);
}

.icon {
  color: var(--information-card-line);
  padding-right: 0.3rem;
}

.button {
  padding-top: 15px;
  padding-bottom: 15px;
}

@media (max-width: 575px) {
  .button {
    padding-top: 10px;
    padding-bottom: 10px;
  }
}
