.item-header {
    padding-left: 5%;
    padding-right: 5%;
    padding-top: 3%;
}

.item-name {
    font-size: xx-large;
    font-weight: bolder;
}

.item-owner {
    font-size: xx-large;
    font-weight: normal;
}

.item-price-dropped {
    color: var(--price-dropped);
    font-style: italic;
}

.item-price {
    font-size: x-large;
    padding-top: 5%;
    font-weight: bolder;
}

.item-sold {
    padding-top: 5%;
    font-style: oblique;
}

.item-more {
    padding-top: 5%;
    font-style: oblique;
}

.item-count {
    padding: 7px 7px 7px 7px !important;
    font-weight: bolder !important;
}

.item-header-button {
    padding-top: 5%;
}

.item-button {
    padding-left: 10%;
}

.item-page-main-header {
    padding-top: 5%;
}

.item-page-sub-header {
    padding-bottom: 15px;
}

.product-details {
    width: 50%;
    padding-top: 2%;
}

.product-details-column {
    float: left;
    width: 50%;
}

.product-notice {
    width: 50%;
    padding-top: 2%;
}

.product-delivery {
    padding-top: 2%;
}

.ratings-container {
    padding-top: 2%;
}

.rating {
    padding-top: 2%;
}

.reviews {
    padding-bottom: 1%;
}