.parent-disable {
    position: fixed;
    top: 0;
    left: 0;
    background: var(--primary-gray);
    opacity: 0.8;
    z-index: 998;
    height: 100%;
    width: 100%;
  }
  
  .overlay-box {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: transparent;
    opacity: 1.0;
    z-index: 1000;
  }
  