.text-span{
    background:var(--modal-text-background-colour);
    border-radius:100%;
    height: 26px;
    width: 26px;
    display: inline-block;
    text-align: center;
}
.closeBtn {
    float: right;
    font-size: 1.5rem;
    font-weight: 700;
    line-height: 1;
    color: var(--modal-closebtn);
    text-shadow: 0 1px 0 var(--modal-closebtn-shadow);
    opacity: .75;
}

.modal-header .closeBtn {
    padding: 1rem 1rem;
    margin: -1rem -2rem 0 auto;
}
.rm-header .closeBtn {
    padding: 1rem 1rem;
    margin: -1rem -2rem 0 auto;
}
.closeBtn:hover {
    color: var(--modal-closebtn-hover);
    text-decoration: none;
}

.closeBtn:not(:disabled):not(.disabled):focus, .closeBtn:not(:disabled):not(.disabled):hover {
    opacity: .85;
}
button.closeBtn {
    padding: 0;
    background-color: transparent;
    border: 0;
}

.modal-dialog {
    max-width: min-content !important;
    margin: 1.75rem auto !important;
}
.modal-header{
    font-size: 3vw;
    font-weight: bold;
}
.body{
    position: relative;
    flex: 1 1 auto;
    padding: 1vw;

}
@media only screen and (max-width: 422px) {
    .text-span{
        height: 15px;
        width: 15px;
    }
    .closeBtn{
        font-size: 1rem;
        
    }
    .modal-header{
        padding: .1rem .1rem !important;
    }

    .closeBtn {
        margin: -.6rem -.7rem 0 auto !important;
    }
    .modal-dialog {
        margin: 1rem auto !important;
    }
}