.flex-gap {
    display: inline-flex;
    flex-wrap: wrap;
    margin-left: 10%;
}

.flex-gap>div {
    margin: 0% 4% 2% 0%;
}

.cover {
    width: 90%;
    height: 20rem;
    align-self: center;
    margin: auto;
    transform: translateY(-160%)
}
.city-name{
    font-size: 3vw;
    font-weight: initial;
     
}

.image {
    object-position: center;
    object-fit: cover;
    overflow: hidden;
}

.detail {
    /* height: 13rem; */
    height: 10em;
    width: 95%;
    background-position: center;
    background: linear-gradient(to left #FFFBF2, #FBF8F1, #F1E7D9);
    background-color: var(--profile-body-color);
    margin: auto;
    display: flex;
    margin-top: 25%;
    /* padding-top: 25%; */
}

.profile {
    margin-left: 10%;
    transform: translateY(-310%)
}

.circular--portrait {
    position: relative;
    width: 200px;
    height: 200px;
    overflow: hidden;
    border-radius: 50%;
    border-style: solid
}

.circular--portrait img {
    width: 100%;
    height: auto;
}



.header {
    font-family: Noto Sans Tamil;
    font-style: normal;
    font-weight: bold;
    font-size: 3em;
    line-height: 2em;
    letter-spacing: 0.02em;
    color: var(--profile-header);
    margin-left: 25%;
    margin-top: 1px;

}
.body{
    flex: 1 1 auto;
    min-height: 1px;
    
}

.font {
    font-family: Taviraj;
    font-style: normal;
    font-weight: 600;
    font-size: 1.5em;
    line-height: 1em;
    /* identical to box height */
    letter-spacing: 0.02em;
    color: var(--profile-font);
}

.box{
    inline-size:  10em;
    overflow-wrap: break-word;
}
.stars {
    /* margin-top:10%; */
    display: flex;
    margin-bottom: 2%;
}
.container-1{
    width: 500px;
    font-size: 2vw;
    /* overflow:scroll; */
}
.container-2{
    width: 500px;
    font-size: 2vw;
    /* overflow:scroll; */
}
.flex-container {
    display: flex;
    width: fit-content;
    
}




