@import "./store-element/index.css";

.single {
  height: 200px;
}

.double {
  height: 416px;
}

.shadow-border {
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 4px 4px 4px 0 rgba(0, 0, 0, 0.2), 6px 6px 6px 0 rgba(0, 0, 0, 0.1);
}

.first {
  padding: 0 1rem 1rem 0;
}

.second {
  padding: 0 0 1rem 0;
}

.forth{
  padding: 0 0 0 1rem;
}

.fifth{
  padding: 0 0 0 1rem;
}

@media (max-width: 767px) {
  .double {
    height: 408px;
  }
  .first {
    padding: 0 0.5rem 0.5rem 0;
  }
  .second {
    padding: 0 0 0.5rem 0;
  }
  .forth {
    padding: 0 0 0 0.5rem;
  }
  .fifth {
    padding: 0 0 0 0.5rem;
  }
}

@media (max-width: 575px) {
  .double {
    height: 200px;
  }
  .fifth {
    padding: 0.5rem 0 0 0;
  }
}

@media (max-width: 450px) {
  .single {
    height: 150px;
  }

  .double {
    height: 150px;
  }
}
