.col {
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
    text-align: center;
}

.cols {
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
    text-align: left;
    display: flex;
    padding: 0 23px 0 23px;
}

.cole {
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
    text-align: right;
    padding-right: 23px;
}

.limheight{
    padding-left: 10px;
}

.limheight-list{
    display: flex;
}

.promo-text-box{
    border: double !important;
    border-width: thin !important;
    width: 122px !important;
}

.off-value{
    color: green !important;
    font-weight: 900 !important;
}

.right-list{
    display: inline;
}

.increment-button-text{
    margin: 0 10px 0 0;
}

.calc{
    margin: 9px 10px 0 0;
}

.checkout-button{
    width: 96%;
    margin: 0 15px 15px 15px;
    color: var(--cart-checkout-btn-text) !important;
}

.trash-icon {
    color: var(--cart-trash);
    font-size: 17px;
}

.right-content {
    display: inline-flex;
    align-items: flex-start;
}

.trash-border {
    border-style: solid;
    border-color: var(--cart-trash);
    align-content: center;
    text-align: center;
    border-width: thin;
    padding: 9px;
}

.item-name {
    margin-top: 10px;
}

.note {
    margin-bottom: 10px;
    display: inline-flex;
    align-items: flex-end;
    flex-direction: column;
}

.item-seperate-bar {
    margin-left: 10px;
    margin-right: 10px;
}

.promo-code-text {
    padding-top: 18px;
}

.promo-code-box {
    padding: 13px 0  0 15px;
}

.off-label {
    padding: 18px 0  0 5px;
}
