.custom-text-box {
  background-color: var(--text-box-text-background) !important;
  transition: 0.3s ease-in-out, 0.3s ease-in-out !important;
}

.custom-text-box-input-default:focus,
.custom-text-box-input-default:hover,
.custom-text-box-input-default:active {
  color: var(--text-box-text-default) !important;
  border-color: var(--text-box-border) !important;
  outline: 0;
  box-shadow: 0 0 0 0.1rem var(--text-box-border) !important;
}

.custom-text-box-helper-text {
  float: left;
  font-size: smaller;
}

.custom-text-box-helper-text-error {
  color: var(--text-box-error);
}

.custom-text-box-helper-text-default {
  color: var(--text-box-text-faded);
}

.custom-text-box-input-error {
  border-color: var(--text-box-error) !important;
  box-shadow: none !important;
}
