.breadcrumb {
    width: fit-content;
    font-size: small;
    background-color: transparent !important;
}

.breadcrumb >.item a {
    color: var(--breadcrumb-text-item);
    text-decoration: none;
}

.breadcrumb >.active {
    color: var(--breadcrumb-text-active) !important;
    font-weight: bold;
}
