@import "./components/atoms/index.css";
@import "./components/molecules/index.css";
@import "./components/organisms/index.css";

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Taviraj',
    sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--background);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

/** Overided styles of react-bootstrap-table library */

.dropdown-item> a {
  color:var(--button-primary-text);
}

.page-link {
  position: relative;
  display: block;
  padding: .5rem .75rem;
  margin-left: -1px;
  line-height: 1.25;
  color:var(--button-primary-text);
  border-color: var(--button-tertiary);
}

.page-link:hover {
  z-index: 2;
  background-color:var(--button-tertiary);
  color:var(--button-primary-text);
  border-color: var(--button-quaternary);
}

.page-item.active> .page-link {
  z-index: 3;
  color: var(--button-primary-text);
  background-color:var(--button-tertiary);
  border-color: var(--button-tertiary);
}

.react-bs-container-body tr {
  border-top-style: hidden;
}

.react-bs-container-header tr {
  border-bottom-style: hidden;
}

/** End of overided styles of react-bootstrap-table library */

/* Styles overidden for Radio Buttons*/
.custom-control-input:checked ~ .custom-control-label::before {
  border-color: var(--checkout-active);
  background-color: var(--checkout-active);
}
