
.outside-box-image{
  padding-top: 10%;
}
.outside-box-steps{
  padding-top: 5%;
}
.logo {
  height: auto;
  width: 160px;
}
.widget-boxs, .detail{
  margin: 'auto';
  padding-left: 5%;
  padding-right: 5%; 

}
.logo-img {
  display: inline-block;
  vertical-align: top;
  max-width: 100%;
  max-height:100%;
}

.input-container{
  width: 400px;
  border: 1px  solid var(--order-tracking-input-boarder);
  display: inline-block;
  border-radius:5px;
  background-color: var(--order-tracking-input-background);
  align-items: center;

}
.input-container input:focus, .input-container input:active {
  outline: none;
}
.input-container input {
  width: 60%;
  border: none;
  height:35px;
}

.input-container button {
  float: right;
}
.tracking-search-box{
  display: flex;
  flex-direction: column;
  align-items: center; 
  width: auto;
  height: min-content;
  
}
.cart-style{
  padding-top: 10vw ;
} 

  .clock-icon , .cook-icon {
    width: 50px;
    height: 50px; 
    margin: auto

  }

  .delivery-icon{
    width: 65px;
    height: 65px;
    margin: auto

  }
  .complete-icon{
    width: 60px; 
    height: 60px;
    margin: auto 

  }

  .steps .step {
    display: block;
    width: 100%;
    margin-bottom: 35px;
    text-align: center
  }
.steps .step .step-icon-wrap {
    display: block;
    position: relative;
    width: 100%;
    height: 80px;
    text-align: center
    
}

.steps .step .step-icon-wrap::before,
.steps .step .step-icon-wrap::after {
    display: block;
    position: absolute;
    top: 50%;
    width: 50%;
    height: 3px;
    margin-top: 3px;
    background-color: var(--order-tracking-step-icon-background);
    content: '';
    z-index: 1
}
.steps .step .step-icon-wrap::before {
    left: 0
}
.steps .step .step-icon-wrap::after {
    right: 0
}
.steps .step .step-icon {
    display: inline-block;
    position: relative;
    width: 2.5em;
    height: 2.5em;
    border: 1px solid var(--order-tracking-step-icon-boarder);
    border-radius: 50%;
    background-color: var(--order-tracking-step-icon-background);
    color: var(--order-tracking-step-icon-colour);
    font-size: 38px;
    line-height: 81px;
    z-index: 5
}
.steps .step .step-title {
    margin-top: 16px;
    margin-bottom: 0;
    color: var(--order-tracking-step-title);
}
.steps .step:first-child .step-icon-wrap::before {
    display: none
}
.steps .step:last-child .step-icon-wrap::after {
    display: none
}
.steps .step.completed .step-icon-wrap::before,
.steps .step.completed .step-icon-wrap::after {
    background-color: var(--order-tracking-step-complete)
}
.steps .step.completed .step-icon {

    border: 3px solid var(--order-tracking-step-complete)
}




@media (max-width: 991px) {
    .logo {
      height: auto;
      width: 140px;
    }
}

@media (max-width: 900px) {

  .input-button {
    font-size: xx-small;
    font-weight: 300;       
  }

  .logo {
    width: 100px;
  }
  .input-container{
    width: 300px;
    display: inline-block;  
  }
  .input-container input {
    width: 60%;
    height:30px;
    font-size: x-small
  }

}
@media (max-width: 768px) {
  .flex-md-nowrap .step .step-icon-wrap::before,
  .flex-md-nowrap .step .step-icon-wrap::after {
      display: none
  }
}

@media (max-width: 576px) {
  .steps .step .step-icon-wrap::before,
  .steps .step .step-icon-wrap::after {

      top: 40%;  
      height: 3px;
      margin-top:5px;

  }  
  .steps .step .step-icon {
    position: relative;
    width: 1.5em;
    height: 1.5em;
    border-radius: 50%;
    font-size: 38px;
    line-height: 81px;
    z-index: 5;
    margin-top: -2px;
}

.steps .step .step-icon-wrap {
  height: 50px;
  
  
}

  .steps .step {
    display: block;
    width: 25%;
    margin-bottom: 35px;
    text-align: center
  }

  .clock-icon , .cook-icon {
    width: 30px;
    height: 30px; 
    margin-top: -30px;

  }

  .delivery-icon{
    width: 35px;
    height: 35px;
    margin-top: -30px;

  }
  .complete-icon{
    width: 40px; 
    height: 40px;
    margin-top: -30px;
  }
}
@media (max-width: 420px) {

   .detail{
    margin-top: -20px;
  } 
  

  }

@media (max-width: 460px) {

  .input-button {
    font-size: 7px ;
    font-weight: 300;
    padding: 5px ;
  }
  .logo {
    width: 80px;
  }
  .input-container{
    width: 220px;
    display: inline-block;  
  }
  .input-container input {
    width: 60%;
    height:22px;
    font-size: x-small
  }
  

  }


  @media (max-width: 330px) {

    .input-button {
      font-size: 6px ;
      font-weight: 300;
      padding: 5px ;
    }
    .logo {
      width: 50px;
    }
    .input-container{
      width: 200px;
      display: inline-block;  
      padding: 0 ;
    }
    .input-container input {
      width: 60%;
      height:15px;
      font-size: 8px;
      
    }
    .cart-style{
      padding-top: 5vw ;
    } 
    }


@media (max-width: 310px) {

  .steps .step .step-icon-wrap::before,
  .steps .step .step-icon-wrap::after {
  
      top: 40%;  
      height: 3px;
      margin-top:7px;
  
  }  
    .steps .step .step-icon {
      width: 1.8em;
      height: 1.8em;
      border-radius: 50%;
      font-size: 20px;
  }
  
  .steps .step .step-icon-wrap {
    height: 20px;
    
    
  }
  
    .clock-icon , .cook-icon {
      width: 20px;
      height: 20px; 
      margin-top: -50px;
  
    }
  
    .delivery-icon{
      width: 25px;
      height: 25px;
      margin-top: -50px;
  
    }
    .complete-icon{
      width: 30px; 
      height: 30px;
      margin-top: -50px;
  
    }
    .outside-box-steps{
      padding-top: 8%;
    }
  }

  @media (max-width: 242px) {

    .input-button {
      font-size: 5px ;
      font-weight: 300;
      padding: 2px ;
    }
    .logo {
      width: 40px;
    }
    .input-container{
      width: 150px;
      display: inline-block;  
      padding-top: 0 ;
      padding-left: 2px ;
      height: 17px ;
    }
    .input-container input {
      width: 60%;
      height:15px;
      font-size: 7px;
      transform: translateY(-8px)
      
    }
    .cart-style{
      padding-top: 5vw ;
    } 

    .padding-top-bottom {
      padding-top: 15px;
      padding-bottom: 15px;
    }
    

    }

