@import './color-palette.css';

:root {
    /* common */
    --error: var(--red);
    --text-chocolate: var(--dark-brown);
    --background: var(--background-white);

    /* label */
    --label-primary: var(--black);
    --label-secondary: var(--dark-brown);
    --label-tertiary: var(--grey);
    --label-darkgrey: var(--dark-grey);
    --label-lightgrey: var(--grey);
    --label-gold: var(--gold);
    --label-dark-brown: var(--dark-brown);

    /*button*/
    --button-primary: var(--gold);
    --button-secondary: var(--white);
    --button-tertiary: var(--dark-gold);
    --button-quaternary: var(--dark-brown);
    --button-primary-text: var(--black);
    --button-secondary-text: var(--black);
    --button-link-text: var(--usc-gold);

    /*text-box*/
    --text-box-border: var(--gold);
    --text-box-text-default: var(--black);
    --text-box-error: var(--red);
    --text-box-text-faded: var(--grey);
    --text-box-text-background: var(--white);

    /* Date Picker */
    --dpk-selected-date: var(--gold);
    --dpk-today-background: var(--cream);
    --dpk-text: var(--dark-grey);
    --dpk-shadow: rgba(0, 0, 0, 0.2);
    --dpk-border: var(--dark-brown);
    --dpk-hover: var(--white);
    --dpk-arrw-hover: var(--dark-grey);
    --dpk-primary-background: var(--white);
    --dpk-secondary-background: var(--white);

    /* breadcrumb */
    --breadcrumb-text-item: var(--light-black);
    --breadcrumb-text-active: var(--dark-brown);

    /*form*/
    --form-link: var(--gold);
    --form-background: var(--white);
    --form-text-default: var(--black);
    --form-error: var(--red);
    --form-shadow:var(--shadow-yellow);
    --form-input-line:var(--grey);

    /* information card */
    --information-card-background: var(--white);
    --information-card-line: var(--grey);

    /*Dropdown*/
    --custom-dropdown-text: var(--black);
    --custom-dropdown-border: var(--gold);
    --custom-dropdown-checked: var(--cream);

    /* Header */
    --header-white: var(--white);
    --header-search-icon: var(--light-grey);
    --header-nav-dropdown-background: var(--light-black);
    --header-cart-count-background: var(--red);

    /* Common Footer */
    --common-footer-about-text: var(--white);
    /*--common-footer-background: #414141;*/
    --common-footer-background: linear-gradient(252.73deg, var(--black-marlin) 26.48%, rgba(154, 154, 154, 0) 71.78%), var(--black-marlin);
    --common-footer-menu-text: var(--white);
    --common-footer-menu-hover: var(--dark-grey);
    --common-footer-icon: var(--white);
    --common-footer-copyright-text: var(--white);

    /* image-grid */
    --image-grid-background: var(--cream);

    /* section heading */
    --section-heading-text: var(--light-black);
    --section-heading-line: var(--black);

    /* featured stores */
    --featured-store-element-header-line: var(--black);

    /*cart*/
    --cart-background: var(--light-grey);
    --cart-trash: var(--red);
    --cart-checkout-btn-text: var(--white);

    /* searchable-dropdown */
    --searchable-dropdown-text: var(--white);
    --searchable-dropdown-background: var(--gold);
    --searchable-dropdown-badge-border-color: var(--black);
    --searchable-dropdown-white: var(--white);
    --searchable-dropdown-grey: var(--grey);
    --searchable-dropdown-lightslategray: var(--light-grey);

    /* item-view-page */
    --price-dropped: var(--grey);

    /* profile */
    --profile-body-color: var(--light-cream);
    --profile-header: var(--dark-grey);
    --profile-font: var(--black);

    /* modal */
    --modal-text-background-colour: var(--dark-brown);
    --modal-closebtn: var(--black);
    --modal-closebtn-shadow: var(--white);
    --modal-closebtn-hover: var(--black);

    /*text-area*/
    --text-area-border: var(--gold);
    --text-area-text-default: var(--black);
    --text-area-error: var(--red);
    --text-area-text-faded: var(--grey);
    /* order-tracking-page */
    --order-tracking-font-colour: var(--black);
    --order-tracking-input-boarder: var(--gold);
    --order-tracking-input-background: var(--white);
    --order-tracking-step-icon-background: var(--light-grey);
    --order-tracking-step-icon-boarder: var(--light-grey);
    --order-tracking-step-icon-colour: var(--grey);
    --order-tracking-step-title: var(--black);
    --order-tracking-step-complete: var(--light-gold);
    --order-tracking-instruction: var(--grey);
    --order-tracking-copy: var(--light-gold);

    --text-area-text-faded: var(--grey);

    /* checkout */
    --checkout-inactive: var(--grey);
    --checkout-active: var(--gold);

    /*Search Page*/
    --search_page-section-background: var(--white);

    /* user-profile */
    --user-profile-verified-symbol-inactive: var(--grey);
    --user-profile-verified-symbol-active: var(--gold);
    --user-profile-address-seperater: var(--shadow-yellow);
    --user-profile-name: var(--dark-grey);
}
